body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 50vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.submitButton,
.newAuthor,
.talkBtn {
  color: #282c34;
  background-color: whitesmoke;
  font-style: inherit;
  font-size: 16px;
  border: 2px solid whitesmoke;
  border-radius: 10px;
  margin: 4vh;
  padding: 10px;
  outline: none;
}

.submitButton:active,
.newAuthor:active,
.talkBtn:active {
  -webkit-transform: scale(0.9);
          transform: scale(0.9);
}

.checkboxThree {
  width: 120px;
  height: 40px;
  background: #333;
  margin: 20px 60px;

  border-radius: 50px;
  position: relative;
}

.checkboxThree:before {
  content: "On";
  position: absolute;
  top: 12px;
  left: 13px;
  height: 2px;
  color: #26ca28;
  font-size: 16px;
}

.checkboxThree:after {
  content: "Off";
  position: absolute;
  top: 12px;
  left: 84px;
  height: 2px;
  color: #ddd;
  font-size: 16px;
}

/**
 * Create the pill to click
 */
.checkboxThree label {
  display: block;
  width: 52px;
  height: 22px;
  border-radius: 50px;

  transition: all 0.5s ease;
  cursor: pointer;
  position: absolute;
  top: 9px;
  z-index: 1;
  left: 12px;
  background: #ddd;
}

/**
* Create the checkbox event for the label
*/
.checkboxThree input[type="checkbox"]:checked + label {
  left: 60px;
  background: #26ca28;
}

input[type="checkbox"] {
  visibility: hidden;
}

